/**
 * TÃ¼m ana ÅŸehir verilerini (alt kasabalar dahil) dÃ¶ndÃ¼rÃ¼r.
 */
export function getAllCities(): City[] {
    return citiesData;
}

/**
 * Belirli bir citySlug'a ait City verilerini dÃ¶ndÃ¼rÃ¼r.
 * @param citySlug - Ana ÅŸehrin slug'Ä± (Ã¶rnek: 'muenchen')
 */
export function getCityData(citySlug: string): City | undefined {
    return citiesData.find(city => city.slug === citySlug);
}

/**
 * Belirli bir citySlug iÃ§indeki townSlug'a ait Town verilerini dÃ¶ndÃ¼rÃ¼r.
 * @param citySlug - Ana ÅŸehrin slug'Ä± (Ã¶rnek: 'dachau')
 * @param townSlug - Alt bÃ¶lgenin slug'Ä± (Ã¶rnek: 'karlsfeld')
 */
export function getTownData(citySlug: string, townSlug: string): Town | undefined {
    const city = getCityData(citySlug);
    if (!city) return undefined;
    return city.towns.find(town => town.slug === townSlug);
}